import { getComments } from '@/api/article'
export default {
       name: 'ArticleComments',
       props: {
           article: {
               type: Object,
               required: true
           }
       },
       data () {
           return {
               comments: []
           }
       },
       async mounted() {
        //  console.log(this.article);
        //  console.log(this.isMineArticle);
         const { data } = await getComments(this.article.slug)  
         this.comments = data.comments
       }
}
